.card-container{
    display: flex;
    width: 220px;
    flex-direction: column;
    box-shadow: 1px 2px 4px #e8e8e87c;
    border-radius: 10px;
}
.card-container:hover{
    box-shadow: 1px 1px 4px #e8e8e8;
}
.img-container{
    border-radius: 10px 10px 0 0;
}
.main-image{
    /* height: 300px; */
    width: 100%;
    border-radius: 10px 10px 0 0;
}
.card-title{
    text-align: center;
    padding: 5px 0;
}
.price{
    text-align: center;
    padding: 5px 0;
    font-weight: 300;
}

@media(max-width:900px){
    .card-container{
        width: 90%;
    }
}