.home-container{
    display: flex;
    flex-direction: column;
    font-family: Poppins;
    
}

.section-header{
    text-align: center;
    font-size: 20px;
    color: #5d5d5d;
    padding: 20px 0;
    letter-spacing: 2px;
}
.cards-container{
    display: grid;
    grid-template-columns: auto auto auto;
    width: 100%;
}
.card{
    margin: auto;
}

@media(max-width:900px){
    .cards-container{
        grid-template-columns: auto auto;
    }
}