.main-container{
    min-height: calc(100vh - 65px);
    display: flex;
    justify-content: center;
}
.component{
    width: 90%;
    max-width: 1200px;
}
.wa-link{
    position: fixed;
    bottom: 30px;
    right: 30px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-radius: 50px;
    box-shadow: 1px 1px 4px #bbb;
    cursor: pointer;
}
.wa-link::after{
    content: '';
    padding: 3px;
    position: absolute;
    border-radius: 50px;
    right: 5px;
    top: 10px;
    background-color: #c53d3d;
}
